import { mapState } from 'vuex';

export default {
  name: 'Home',
  async fetch({ store }) {
    await store.dispatch('Main/getMain');
  },
  data() {
    return {
      REGION: process.env.REGION,
      swiperOptions: {
        autoplay: {
          delay: 2500,
          disableOnInteraction: false,
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
    };
  },
  computed: {
    ...mapState('Nav', ['index']),
    ...mapState('Main', ['mainLoading', 'banners', 'news', 'tick']),
  },
};
