import utils from '@/utils';

export default {
  name: 'News',
  props: {
    news: {
      type: Array,
      default: () => {},
    },
  },
  data() {
    return {
      REGION: process.env.REGION,
    };
  },
  computed: {
    transformTime() {
      return (time) => {
        if (process.env.REGION === 'vn') {
          return utils.timeToHHMMDDMMYY(time);
        }
        return utils.timeToYYMMDDHHMM(time);
      };
    },
  },
};
